import { defineComponent, ref } from '@vue/composition-api';
import Jsonschema2Form, {
  NumberType,
  SelectType,
  SelectType2,
  StringType,
  IntegerType,
} from '@4dst-saas/form-utils/dist/components/jsonschema2form';
import Jsonschema from '@4dst-saas/form-utils/dist/components/jsonschema2form/JSONschema';
import { VNode } from 'vue/types/umd';
import antaJson from './AntaFormJson.json';
// import SelectType2 from './SelectType2';


// eslint-disable-next-line consistent-return
function formItemGenerator(
  // eslint-disable-next-line no-use-before-define
  this: InstanceType<typeof formTest>,
  jss: Jsonschema,
  form: { [key: string]: any },
  key: string,
  attrs: Partial<Jsonschema>,
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  h = this.$createElement,
): VNode | undefined {
  if (jss.custom) {
    // let slot: VNode;
    switch (jss.customType) {
      case 'selector2': {
        // eslint-disable-next-line no-case-declarations
        const { baseString, from, to } = jss as Jsonschema & {
              baseString?: string,
              from: number,
              to: number
            };
        // eslint-disable-next-line no-case-declarations
        const options: string[] | number[] = [];
        if (baseString) {
          for (let i = from; i <= to; i++) {
            options.push(`${baseString}${i}`);
          }
        } else {
          for (let i = from; i <= to; i++) {
            options.push(i);
          }
        }
        // eslint-disable-next-line no-use-before-define
        return <SelectType2 options={options} vModel={form[key]}></SelectType2>;
      }
      default: return <div>custom default</div>;
    }
  }
  // 返回空或不返回会做默认处理
}
// 效果见http://localhost:9200/test/dynamic-form-test
const formTest = defineComponent({
  render() {
    return <div>
      <NumberType vModel={this.num}></NumberType>
      <SelectType vModel={this.sel}></SelectType>
      <StringType vModel={this.str}></StringType>
      <IntegerType vModel={this.int}></IntegerType>
      <Jsonschema2Form jsonschema={antaJson} formItemGenerator={formItemGenerator}></Jsonschema2Form>
    </div>;
  },
  setup() {
    return {
      num: ref(null),
      sel: ref(null),
      str: ref(null),
      int: ref(null),
    };
  },
});

export default formTest;
